.modal {
    width: 100vw;
    height: 100vh;
    background-color: rgba(0, 0, 0, .4);
    position: fixed;
    top: 0;
    left: 0;
    transform: scale(0);
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal.active {
    transform: scale(1);
}

.modal__content {
    position: absolute;
    top: 110px;
    max-height: 600px;
    max-width: 400px;
    overflow: auto;
    background-color: #1f1f1f;
    transform: translateX(-50%);
    padding: 10px;
    border-radius: 10px;
    color: white;
}

.modal__content:after {
    content: "";
    height: 10px;
    width: 10px;
    background-color: #1f1f1f;
    position: absolute;
    transform: translate(-50%, -50%) rotate(-45deg);
    top: 0;
    left: 50%;
}

.modal__header {
    display: flex;
    flex-flow: row;
    justify-content: space-between;
    align-items: center;
}

.modal__headerBtn {
    background-color: transparent;
    border: none;
    color: rgba(211, 211, 211, .7);
    padding: 5px 10px;
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    font-weight: 500;
    transition: background-color .2s ease-in-out;
}

.modal__headerBtn:hover {
    background-color: #333;
}

