* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#root {
    background-color: #141414;
    font-family: 'Roboto', sans-serif;
    font-weight: 100;
    color: white;
}

.container {
    width: 100%;
    max-width: 1200px;
    padding: 0 10px;
    margin: 0 auto;
}

.flex {
    display: flex;
    flex-flow: column;
    position: relative;
}

/* Navigation bar */
.navbar {
    position: fixed;
    top: 0;
    height: 70px;
    width: 100%;
    background-color: #1f1f1f;
    z-index: 100;
    font-weight: 600;
}

.navbar__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 70px;
}

.navbar__section {
    display: flex;
    flex-flow: row;
    gap: 30px;
}

.navbar__home {
    margin-left: -15px;
    display: flex;
    align-items: center;
    height: 100%;
    padding: 15px 25px 15px 15px;
    transition: all .2s linear;
    text-decoration: none;
    color: white;
    font-size: 18px;
    cursor: pointer;
}

.navbar__home:hover {
    background-color: #333;
}

.navbar__item {
    display: flex;
    align-items: center;
    transition: all .2s linear;
    text-decoration: none;
    color: rgba(211, 211, 211, .7);
    font-size: 18px;
    cursor: pointer;
}

.navbar__item:hover {
    color: white;
}

.navbar__item:last-child {
    margin-right: 0;
}

.active {
    position: relative;
    color: white;
}

.active:after {
    content: "";
    position: absolute;
    width: 60%;
    bottom: -10px;
    right: 50%;
    transform: translateX(50%);
    height: 2px;
    background-color: #c55b2a;
    animation: active .3s ease-in-out;
}

.navbar__logo {
    width: 40px;
    height: 40px;
}

.navbar__title {
    font-size: 25px;
    margin-left: 10px;
    font-style: italic;
}
/* End of Navigation bar */

/* Content */
.content {
    padding-top: 80px;
    position: relative;
}
/* End of Content*/

.filmHeader {
    position: relative;
    height: 93vh;
    margin-top: -10px;
}

.filmHeader__overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, rgb(20,20,20) 0%, rgba(20, 20, 20, .6) 50%, rgb(20,20,20) 100%);
}

.filmHeader__wrapper {
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    height: 93vh;
}

.film__section {
    display: flex;
    align-items: center;
}

.film__section h1{
    font-size: 80px;
}

.film__section h2{
    font-size: 40px;
}

.film__section p{
    font-size: 25px;
    margin-right: 1rem;
    max-height: 6em;
}

.film__section span {
    margin-left: 20px;
}

.film__section a{
    font-size: 30px;
    margin-right: 1rem;
    cursor: pointer;
    text-decoration: none;
    color: white;
}

.film__section a:hover {
    color: #c55b2a;
    text-decoration: underline;
}

.filmHeader .film__section:first-child {
    display: block;
    margin-top: 10vh;
}

.film__section:last-child {
    margin-bottom: 10vh;
}

.filmHeader__icon {
    height: 50px;
    width: 50px;
    margin-right: 1rem;
}

.filmBody .film__section {
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 10vh;
}

.filmBody .film__section:last-child {
    flex-flow: column;
}

.actorsPoster__wrapper {
    width: 100%;
    margin-top: 1rem;
    padding-bottom: 10rem;
}

@keyframes active {
    from {
        width: 0;
    }

    to {
        width: 60%;
    }
}




