.carousel__wrapper {
    width: 100%;
    overflow-x: hidden;
    position: relative;
}

.carousel {
    display: flex;
    flex-flow: row;
    justify-content: space-between;
    transition: all .3s ease-in-out;
}

.carousel img {
    width: 200px;
    height: 300px;
    margin-right: 20px;
    border-radius: 20px;
    object-fit: cover;
}

.carousel div {
    padding-top: 1rem;
    position: relative;
    text-align: center;
    cursor: pointer;
    transition: all .3s ease-in-out;
}

.carousel div:hover {
    transform: translateY(-1rem);
}

.carousel p {
    position: absolute;
    bottom: 5px;
    left: 46%;
    transform: translateX(-50%);
}

.carousel div:before {
    position: absolute;
    content: "";
    background-color: #141414;
    opacity: .5;
    width: 100%;
    height: 5rem;
    bottom: 0;
}

.prevBtn {
    position: absolute;
    width: 50px;
    height: 100%;
    bottom: 0;
    left: 0;
    z-index: 1;
    background-color: rgba(20, 20, 20, .6);
    border: none;
    cursor: pointer;
    transition: all .2s linear;
}

.prevBtn:before {
    content: "";
    width: 12px;
    height: 12px;
    border:  solid rgba(211, 211, 211, .7);
    border-width: 4px 0 0 4px;
    transform: translate(-50%, -50%) rotate(-45deg);
    position: absolute;
    left: 50%;
    top: 50%;
    transition: all .2s linear;
}

.prevBtn:hover:before {
    border:  solid white;
    border-width: 4px 0 0 4px;
}

.nextBtn {
    position: absolute;
    width: 50px;
    height: 100%;
    bottom: 0;
    right: 0;
    z-index: 1;
    background-color: rgba(20, 20, 20, .6);
    border: none;
    cursor: pointer;
    transition: all .2s linear;
}

.nextBtn:before {
    content: "";
    width: 12px;
    height: 12px;
    border:  solid rgba(211, 211, 211, .7);
    border-width: 4px 0 0 4px;
    transform: translate(-50%, -50%) rotate(135deg);
    position: absolute;
    left: 50%;
    top: 50%;
    transition: all .2s linear;
}

.nextBtn:hover:before {
    border:  solid white;
    border-width: 4px 0 0 4px;
}